import { getNode } from "./getNode.js";

export function addClass(node,className){
  
  if(typeof node === 'string') node = getNode(node);
  if(typeof className !== 'string'){
    throw new TypeError('addClass Ìï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.')
  }

  node.classList.add(className);
}

export function removeClass(node,className){
  
  if(typeof node === 'string') node = getNode(node);
  if(!className){
    node.className = '';
    // attr()
    return;
  }
  if(typeof className !== 'string'){
    throw new TypeError('removeClass Ìï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.')
  }
  node.classList.remove(className);
}

export function toggleClass(node,className){
  
  if(typeof node === 'string') node = getNode(node);
  if(typeof className !== 'string'){
    throw new TypeError('toggleClass Ìï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.')
  }

  return node.classList.toggle(className);
}










/* -------------------------------------------- */
/*                   style Ìï®Ïàò                   */
/* -------------------------------------------- */


function getCss(node,prop){

  if(typeof node === 'string') node = getNode(node);
  if(!(prop in document.body.style)){
    throw new SyntaxError('getCss Ìï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Ïú†Ìö®Ìïú css ÏÜçÏÑ±Ïù¥ ÏïÑÎãôÎãàÎã§.')
  }
  return getComputedStyle(node).getPropertyValue(prop)
}

function setCss(node,prop,value){
  
  if(typeof node === 'string') node = getNode(node);
  if(!(prop in document.body.style)){
    throw new SyntaxError('setCss Ìï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Ïú†Ìö®Ìïú css ÏÜçÏÑ±Ïù¥ ÏïÑÎãôÎãàÎã§.')
  }
  if(!value) throw new SyntaxError('setCss Ìï®ÏàòÏùò ÏÑ∏ Î≤àÏß∏ Ïù∏ÏàòÎäî ÌïÑÏàòÍ∞íÏûÖÎãàÎã§.')
  node.style[prop] = value;
}

export const css = (node,prop,value)=> !value ? getCss(node,prop) : setCss(node,prop,value);



