function getAttr(node,prop){

  if(typeof node === 'string') node = getNode(node)
  if(typeof prop !== 'string') throw new TypeError('getAttr Ìï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.');
  
  return node.getAttribute(prop)

}

function setAttr(node,prop,value){
  
  if(isString(node)) node = getNode(node);
  if(!isString(prop)) typeError('setAttrÌï®ÏàòÏùò Îëê Î≤àÏß∏ Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.');

  if(value === ''){
    node.removeAttribute(prop);
    return;
  }

  if(!value) throw new Error('setAttrÌï®ÏàòÏùò ÏÑ∏ Î≤àÏß∏ Ïù∏ÏàòÎäî ÌïÑÏàò ÏûÖÎ†•Í∞í ÏûÖÎãàÎã§.')

  if(prop.startsWith('data')){
    prop = prop.slice(5);
    node.dataset[prop] = value;
    return;
  }

  node.setAttribute(prop,value)
}

export const attr = (node,prop,value) => value === undefined ? getAttr(node,prop) : setAttr(node,prop,value)


