export function getNode(node, context = document) {
  if (typeof node !== "string") {
    throw new Error("getNode Ìï®ÏàòÏùò Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.");
  }

  if(context.nodeType !== document.DOCUMENT_NODE){
    context = document.querySelector(context);
  }

  return context.querySelector(node);
}

export function getNodes(node) {
  if (typeof node !== "string") {
    throw new Error("getNodes Ìï®ÏàòÏùò Ïù∏ÏàòÎäî Î¨∏Ïûê ÌÉÄÏûÖ Ïù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.");
  }

  return document.querySelectorAll(node);
}
