export const memo = (()=>{
  const cache = {}
  
  return (key,callback)=>{
    if(!callback) return cache[key];

    if(cache[key]){
      console.warn(`${key} ÏïàÏóêÎäî Ïù¥ÎØ∏ Ï∫êÏãúÎêú Í∞íÏù¥ Ï°¥Ïû¨Ìï©ÎãàÎã§.`);
      return cache[key]
    }
    cache[key] = callback();
  }

})()




